$bor-bottom: 1px solid rgba(255, 255, 255, 0.5);
@import "index";

.recipe-info-container {
    margin: 7rem auto 2rem;
    padding: 1rem;
    width: 60%;
    background-color: black;
    color: white;
    border-radius: 0.5rem;

    .recipe-title-img {
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: $bor-bottom;

        .recipe-info-title {
            font-size: 2.5rem;
            text-align: center;
        }
        
        .recipe-info-image {
            width: 80%;
            height: auto;
            margin: 1rem auto 0;
            display: block;
        }

        .source-ph {
            margin-bottom: 2rem;
            .link-to-source {
                color: white;
                font-size: 0.8rem;
            }
            .link-to-source:hover {
                color: $mid-red;
            }
        }
    }

    .recipe-details {
        display: flex;
        justify-content: space-evenly;
        margin-top: 0.5rem;

        .recipe-details-left {
            width: 50%;
            margin-right: 0.5rem;
        }
        .recipe-details-right {
            width: 60%;
            margin-left: 0.5rem;
        }

        .recipe-details-left>li, .recipe-details-right>li {
            border-bottom: $bor-bottom;
            margin: 0.5rem;
            font-weight: bold;

            p, li {
                font-weight: 100;
                margin: 0.5rem;
            }
        }
    }
}

@media screen and (max-width: 420px) {
    .recipe-info-container {
        margin: 4rem auto 2rem;
        padding: 0.1rem;
        width: 100%;

        .recipe-details {
            margin-top: 0.5rem;
            
            .recipe-details-left {
                width: 50%;
                margin-right: 0.2rem;
                border-right: $bor-bottom;
            }
            .recipe-details-right {
                width: 50%;
                margin-left: 0.2rem;
            }
        }
    }
}
@media screen and (max-width: 768px) and (min-width: 421px) {
    .recipe-info-container {
        margin: 4rem auto;
        padding: 0.5rem;
        width: 90%;
        
        .recipe-details {
            margin-top: 0.5rem;
            
            .recipe-details-left {
                width: 50%;
                margin-right: 0.2rem;
                border-right: $bor-bottom;
            }
            .recipe-details-right {
                width: 50%;
                margin-left: 0.2rem;
            }
        }
    }
}